export const BLOCKQUOTE_TOGGLE = Symbol('BLOCKQUOTE_TOGGLE');
export const BLOCKQUOTE_ENABLE = Symbol('BLOCKQUOTE_ENABLE');
export const BLOCKQUOTE_TRANSFORM = Symbol('BLOCKQUOTE_TRANSFORM');
export const BLOCKQUOTE_DISABLE = Symbol('BLOCKQUOTE_DISABLE');

export function toggleBlockquote() {
  return {
    type: BLOCKQUOTE_TOGGLE,
  };
}

export function transformBlockquote() {
  return {
    type: BLOCKQUOTE_TRANSFORM,
  };
}

export function enableBlockquote() {
  return {
    type: BLOCKQUOTE_ENABLE,
  };
}

export function disableBlockquote() {
  return {
    type: BLOCKQUOTE_DISABLE,
  };
}

